import React, { useState } from 'react'
import './SearchPanel.scss';

const SearchPanel = () => {
    const [term, setTerm] = useState("");
    const [equals, setEquals] = useState("");
    const [contains, setContains] = useState("");
    const [reg, setReg] = useState("");



    const handleChange = (event: any) => {
        //setState({value: event.target.value});
        setTerm(event.target.value);
    }

    const handleSubmit = (event: any) => {
        alert('A term was entered: ' + term);
        event.preventDefault();
    }
    return (
        <div>
            <form onSubmit={handleSubmit}>
                <label>Term/Word:
                    <input type="text" value={term} onChange={handleChange} />
                </label>
                <label>Equals:
                    <input type="text" value={equals} onChange={handleChange} />
                </label>
                <label>Contains:
                    <input type="text" value={contains} onChange={handleChange} />
                </label>
                <label>Regex:
                    <input type="text" value={reg} onChange={handleChange} />
                </label>
                <button id="Query-button" type="submit" value="Submit">Run Query</button>
            </form>  
        </div>
    )
}

export default SearchPanel
